<template>
  <div id="alert" class="ma-6">
    <transition name="slide-left">
      <v-alert
        v-if="success"
        dense
        class="text-left"
        type="success"
        color="info"
      >
        Task Successfuly Submited
      </v-alert>
      <v-alert v-else-if="warning" type="warning" class="text-left"
        >Cannot Save Task With Empty Time or Date</v-alert
      >
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Alert',
  components: {
    VAlert: () => import('vuetify/lib')
  },
  props: {
    success: Boolean,
    warning: Boolean
  }
};
</script>

<style lang="scss" scoped>
#alert {
  position: absolute;
  z-index: 11;
  top: 0;
  left: 0;
  right: 0;
}
</style>
